<template>
  <div>
  
    <div class="van-list">
      <!-- <van-pull-refresh > -->
    <van-nav-bar title="公告列表"  left-arrow @click-left="$router.go(-1)">
    </van-nav-bar>

      <van-list v-model="loadingDown" 
      :finished="finished"
       @load='getData'
       style="background-color:#f0f0f0">
  
        <noticeItem v-for="(item,index) in coupons" :key="index" :articles='item'></noticeItem>
      </van-list>
      <!-- </van-pull-refresh> -->
    </div>
  </div>
</template>

<script>

  import {
    PullRefresh,
    List,
     NavBar, Icon
  } from "vant";
  // 拿专家的详情来用
  import noticeItem from './components/noticeItem.vue'
  
  export default {
    data() {
      return {
        chosenCoupon: -1,
        coupons: [],
        // disabledCoupons: [coupon],
        showList: false,
        loadingUp: false,
        finished: false,
        loadingDown: false,
        pageSize:10,
        pageNum:0
      }
    },
    methods: {
      getData() {
        this.loadingDown = true
        let params = {
          pageSize:this.pageSize,
          pageNum:this.pageNum
        }
        this.$get(this.$api.getNotice,{params}).then(res => {
          this.coupons.push(...res)
          this.loadingDown = false
          this.pageNum++
          if (res.length < 10) {
            this.finished = true
          }
        })
      },
  
    },
    components: {
      [PullRefresh.name]: PullRefresh,
      [List.name]: List,
      noticeItem,
      [NavBar.name]: NavBar,
      
      [Icon.name]: Icon,
    },
    created() {
      this.getData()
    }
  }
</script>

<style lang="less" scoped>
  .swiper-img-height {
    height: 155px;
    display: block;
    width: 100%;
  }
</style>
